@mixin standart_main_container() {
  min-height: 100vh;
  height: 100%;
  background: #1d1d1d;
  display: flex;
  margin-left: -117vw;
  position: relative;
  transition: 1s all;
  color: #fff;
}

@mixin active_wrapper() {
  margin-left: 0;

  @include media-breakpoint-down(xs) {
    margin-left: 0;
  }
}

@mixin button() {
  color: #08fdd8;
  font-size: 20px;
  letter-spacing: 0.15rem;
  font-weight: 300;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
  margin-top: 20px;
  padding: 8px 20px;
  line-height: 50px;
  display: block;
  text-align: center;
  border: 1px solid #08fdd8;
  cursor: pointer;
  background: rgba(255, 255, 255, 0);
  background-image: linear-gradient(45deg, transparent 50%, rgba(8, 253, 216, 70%) 50%);
  background-position: 25%;
  background-size: 400%;
  -webkit-transition: background 700ms ease-in-out, color 700ms ease-in-out;
  transition: background 700ms ease-in-out, color 700ms ease-in-out;

  &:hover {
    //padding: 10px 35px;
    color: #1d1d1d;
    background-position: 100%;
  }

  @include media-breakpoint-down(md) {
    padding: 10px 20px;

    &:hover {
      padding: 10px 25px;
    }
  }
  @include media-breakpoint-down(xs) {
    padding: 7px 10px;
  }
}

