@import 'src/styles/media-query';
@import 'src/styles/mixin';

@font-face {
  font-family: Cyber;
  src: url('https://assets.codepen.io/605876/Blender-Pro-Bold.otf');
  font-display: swap;
}

.home {
  @include standart_main_container;
}

.active {
  @include active_wrapper;
}

.container {
  display: flex;
  align-items: center;

  @include media-breakpoint-down(xs) {
    width: 100vw;
  }
}

.content {
  display: flex;
  justify-content: center;

  @include media-breakpoint-down(xl) {
    flex-direction: column-reverse;
  }

  @include media-breakpoint-down(xs) {
    width: 90vw;
    margin-left: -12%;
  }
}

.avatar {
  flex: 0.5;
  display: flex;
  align-items: center;

  position: relative;
  justify-content: center;

  @include media-breakpoint-down(xs) {
    display: none;
  }
}

// .box_img {
// 	border: 4px solid #08fdd8;
// 	max-height: 400px;
// 	max-width: 550px;
// }

.avatar img {
  max-height: 450px;
  height: 100%;
  box-shadow: 30px 20px 10px #333;
  position: relative;
  margin: 30px 0 0 30px;
  z-index: 3;
  @include media-breakpoint-down(xxl) {
    max-height: 400px;
  }
  @include media-breakpoint-down(xl) {
    max-height: 300px;
    border-radius: 10px;
    margin-bottom: 2rem;
  }

  @include media-breakpoint-down(md) {
    max-height: 250px;
  }
}

.info {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  height: inherit;
  position: relative;
  z-index: 4;
  //border: .1px solid #fff;

  @include media-breakpoint-down(xl) {
    //padding-left: 50px;
  }
  @include media-breakpoint-down(xs) {
    width: 96vw;
    //padding: 10px;
  }
}

.logo {
  color: #08fdd8;
  font-size: 42px;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
  font-weight: 900;
}

.text {
  font-family: 'Open Sans', sans-serif;
  color: #fff;
  font-size: 1.4rem;
  line-height: 1.5;
  max-width: 90%;
  margin-bottom: 2.5rem;

  @include media-breakpoint-down(xl) {
    margin-bottom: 1.5rem;
  }
  @include media-breakpoint-down(xs) {
    max-width: 95%;

    &:nth-child(3) {
      margin-left: 40px;
      max-width: 85%;
    }
  }

  @include media-breakpoint-down(xxxs) {
    font-size: 1.25rem;
    max-width: 90%;
  }
}

.runText {
  color: #08fdd8;
  font-weight: 900;
  font-size: 26px;
  margin-left: 10px;
  letter-spacing: 4px;
  font-family: 'Open Sans', sans-serif;
}

.linkBox {
  display: flex;
  max-width: 500px;
  width: 100%;
  justify-content: space-between;

  @include media-breakpoint-down(md) {
    max-width: 450px;
  }

  @include media-breakpoint-down(xs) {
    max-width: 380px;
  }
  @include media-breakpoint-down(xxxs) {
    flex-direction: column;
    max-width: 280px;
  }
}

